<template>
    <div class="fabu">
        <van-form @submit="onSubmit">
        <van-field
            v-model="name"
            required
            name="name"
            label="物品名称"
            placeholder="物品名称"
            :rules="[{ required: true, message: '请填写物品名称' }]"
        />
        <van-field
        readonly
        clickable
        name="picker"
        :value="value"
        label="类型"
        placeholder="点击选择类型"
        @click="showPicker = true"
        />
        <van-popup v-model="showPicker" position="bottom">
        <van-picker
            show-toolbar
            :columns="columns"
            @confirm="onConfirm"
            @cancel="showPicker = false"
        />
        </van-popup>
        <van-field
            v-model="price"
            required
            name="price"
            label="价格"
            placeholder="价格"
            :rules="[{ required: true, message: '请填写价格' }]"
        />
        <van-field required name="uploader" label="文件上传">
        <template #input>
            <van-uploader :after-read="afterRead" :max-count="3" v-model="uploader" />
        </template>
        </van-field>
        <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">提交</van-button>
        </div>
        </van-form>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                name: '',
                password: '',
                price:'',
                value: '',
                columns: ['书籍', '电子产品', '3', '4', '5'],
                showPicker: false,
                uploader: [{ url: 'https://img01.yzcdn.cn/vant/leaf.jpg' }],
            }
        },
        methods:{
            afterRead(file){
                this.$api.postupload({file:file}).then(res=>{})
                console.log(file);
            },
            onSubmit(values) {
            console.log('submit', values);
            },
            onConfirm(value) {
            this.value = value;
            this.showPicker = false;
            },
        },
        mounted(){

        }
    }
</script>

<style lang="less" scoped>

</style>
